
import type { Metadata } from 'next';
import { GeistSans } from 'geist/font/sans'; // Import directly from geist package
import { GeistMono } from 'geist/font/mono'; // Import directly from geist package
import './globals.css';
import { UserProvider } from '@/context/UserContext';
import { ThemeProvider } from '@/context/ThemeContext';
import { ClientOnlyWrapper } from '@/components/utils/ClientOnlyWrapper';
// import { Toaster } from "@/components/ui/toaster"; // Toaster can be re-enabled later
// import { ThemeSwitcherWidget } from '@/components/ThemeSwitcherWidget'; // Widget can be re-enabled later

export const metadata: Metadata = {
  title: 'SmartDVM',
  description: 'Your trusted partner in pet health management.',
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en" className={`${GeistSans.variable} ${GeistMono.variable}`} suppressHydrationWarning>
      {/* 
        GeistSans.variable and GeistMono.variable from the 'geist' package 
        provide the necessary CSS custom property names (e.g., '--font-geist-sans').
        These are applied to the <html> tag to make the variables globally available.
      */}
      <body className="antialiased"> {/* Font family is applied via globals.css using the CSS variables */}
        <ClientOnlyWrapper>
          <UserProvider>
            <ThemeProvider>
              {children}
              {/* <Toaster /> */} {/* Temporarily commented out */}
              {/* <ThemeSwitcherWidget /> */} {/* Temporarily commented out */}
            </ThemeProvider>
          </UserProvider>
        </ClientOnlyWrapper>
      </body>
    </html>
  );
}
